
.skills{
    box-sizing: border-box;
    height: 80vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.skills > div {
    flex: 1;
    text-align: center;
    margin: auto;
    border-right: 1px solid #dedede;
}
.skills > div:last-of-type{
    border: none;
}
